import { Link } from 'react-router-dom';
import '../../css/CSSadmin/adminNav2.css'

const NewNav = () => {
  return (
    <nav className="newNav">
      <ul className="navLinks">
        <li>
          <Link to="/admin" className="navLink" ></Link>
        </li>
        <li>
          <Link to="/admin/MemberTable" className="navLink"></Link>
        </li>
        <li>
          <Link to="/admin/ReportListPage" className="navLink"></Link>
        </li>
        <li>
        <Link to="/admin/InquiryList" className="navLink"></Link>
        </li>
      </ul>
    </nav>
  );
};

export default NewNav;
